@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  color: rgb(97, 218, 251);
  outline: none !important;
}
*:focus,
*:active {
  outline: none !important;
}
body{
  background-color: #282c34;
}
.button{
  background: none;
  border: 1px solid rgb(97, 218, 251);
  margin: 5px 0;
  outline: none;
  padding: 10px 20px;
  border-radius: 5px;
  margin-right: 10px;
  cursor: pointer;
}
.button:hover{
  background: rgba(97, 218, 251, 0.1);
}
.input{
  padding: 10px 5px;
  outline: none;
  font-size: 17px;
  background: none;
  border: none;
  width: 100%;
  text-align: left;
  display: block;
}
.input:focus {outline: none !important;}
.input::-moz-focus-inner {border: 0px !important;}
.App{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.board{
  width: calc(64px * 8);
  height: calc(64px * 8);
  display: flex;
  flex-wrap: wrap;
}
.cell{
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cell img{
  width: 48px;
  height: 48px;
  position: relative;
}

.black{
  background-color: rgb(112, 112, 112);
}
.white{
  background-color: #fff;
}

.selected{
  background-color: brown;
}
.avaiable{
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: rgb(0, 192, 26);
}
.lost{
  height: calc(50vh - 60px);
  padding: 30px;
  margin-left: 50px;
  background-color: lightgray;
  color: black;
}
.lost h3{
  color: black;

}
.lostItem{
  color: black;
}